$second_color: #eee;

.introduce {
    width: 100%;
    padding-top: 200px;
    .title {
        color: white;
        font-size: 50px;
        margin-bottom: 20px;
    }
    .desc {
        line-height: 2;
        color: $second_color;
        font-size: 20px;
    }

    .btn {
        margin-right: 20px;
        text-align: center;
        padding: 0 40px;
        height: 60px;
        line-height: 60px;
        color: white;
        border-radius: 5px;
        font-size: 16px;
        border: none;
        letter-spacing: .8px;
        font-weight: 600;
        &.btn--orange {
            -webkit-box-shadow: 0 3px 32px rgb(236 117 81 / 54%);
            box-shadow: 0 3px 32px rgb(236 117 81 / 54%);
            background-image: -webkit-gradient(linear,left top,right top,from(#ed8a4c),to(#ea485c));
            background-image: -webkit-linear-gradient(left,#ed8a4c 0,#ea485c 100%);
            background-image: -o-linear-gradient(left,#ed8a4c 0,#ea485c 100%);
            background-image: linear-gradient(to right,#ed8a4c 0,#ea485c 100%);
            -webkit-transition: -webkit-box-shadow .4s;
            transition: -webkit-box-shadow .4s;
            -o-transition: box-shadow .4s;
            transition: box-shadow .4s;
            transition: box-shadow .4s,-webkit-box-shadow .4s;
        }
        &.btn--blue {
            -webkit-box-shadow: 0 3px 32px rgb(0 231 234 / 54%);
            box-shadow: 0 3px 32px rgb(0 231 234 / 54%);
            background-color: #00cde4;
            -webkit-transition: background-color .4s,-webkit-box-shadow .4s;
            transition: background-color .4s,-webkit-box-shadow .4s;
            -o-transition: background-color .4s,box-shadow .4s;
            transition: background-color .4s,box-shadow .4s;
            transition: background-color .4s,box-shadow .4s,-webkit-box-shadow .4s;
        }
    }

    @media (max-width: 960px) {
        .logo {
            display: none;
        }
    }
}

@media (max-width: 960px) {
    .introduce {
        padding-top: 100px;
    }
}