@import "./config";
@import "./padding";

@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/montserrat.regular.ttf');
}

* {
    margin: 0;
    padding: 0;
}

html {
	scroll-behavior: smooth;
}

body {
    font-family: "Montserrat";
    // background: linear-gradient(45deg, #161c24 0%, #4e565eaa 100%);
    background: linear-gradient(45deg, #dde7f1 0%, #edf7ff 100%);
}

a {
    text-decoration: unset;
    color: unset;
}

button {
    border: unset;

    &:disabled {
        cursor: not-allowed !important;
    }
}

.btn {
    cursor: pointer;
}

.page-content {

    margin-top: 50px;
    margin-inline-start: 230px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
@media (max-width: 960px){
    .page-content {
        margin-inline-start: 0;
    }
}

.d-flex {
    display: flex;
}

.justify-center {
    justify-content: center;
}

.space-around {
    justify-content: space-around;
}

.space-between {
    justify-content: space-between;
}

.container {
    // width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 576px){
    .container {
        max-width: 540px;
    }
}

@media (min-width: 768px){
    .container {
        max-width: 720px;
    }
}

@media (min-width: 992px){
    .container {
        max-width: 960px;
    }
}

@media (min-width: 1200px){
    .container {
        max-width: 1140px;
    }
}


.row {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, .col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, .col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

@media (min-width: 992px){
    .col-lg-4 {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 33.33333%;
        -ms-flex: 0 0 33.33333%;
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
    }
}

.footer {
    padding: 50px;
    .logo__img {
        height: 70px;
    }
    .logo {
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        transition: opacity .4s;
        position: relative;
        top: -3.5px;
    }
    .logo__title {
        font-size: 27px;
    }

    .social-list {
        list-style-type: none;
        padding: 0;
        margin: 0;
        display: flex;
        align-items: center;
        -webkit-box-align: center;
        li{
            margin-right: 10px;
        }
    }

    .social-list__link {
        width: 40px;
        height: 40px;
        position: relative;
        -o-transition: border .4s;
        transition: border .4s;
        display: block;
        .icon {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%,-50%);
            -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
            width: 40px;
        }
    }
    .social-block {
        .social-block__title {
            margin-bottom: 20px;
            font-size: 22px;
        }
    }
    .copyright {
        color: $second_color;
    }
}


.fontello-icon {
    font-family: fontello;
    font-style: normal;
    font-weight: 400;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    color: #fff;
}