.transfer {
    width: 100%;
    font-size: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;

    .center {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 10px;

        .left {
            width: 76%;

            .stake {
                color: #000;
                background-color: #fdffff;
                border-radius: 10px;
                box-shadow: 0 0 20px #ffffff;
                padding-top: 30px;
                padding-bottom: 50px;
                text-align: center;

                .title {

                }

                .intro {
                    margin-top: 10px;
                    font-size: 14px;
                }

                .function {
                    margin-top: 20px;
                    display: flex;
                    justify-content: center;
                    align-items: flex-start;
                    gap: 20px;

                    div {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;

                        input {
                            background: #f3f2f7;
                            border: 3px solid #6b6e7c61;
                            border-radius: 4px;
                            height: 30px;
                            width: 190px;
                            padding-left: 5px;
                            box-shadow: 3px 4px 4px #9294a063;
                            font-family: "Montserrat";
                            color: #abbac6;
                            font-weight: 500;
                        }

                        span {
                            color: red;
                            font-size: 14px;
                        }
                    }

                    button {
                        border-radius: 7px;
                        display: inline-block;
                        width: 150px;
                        height: 36px;
                        text-align: center;
                        cursor: pointer;
                        background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);
                        box-shadow: 2px 4px 4px #04297a60;
                        color: #ffffffa8;
                        font-family: "Montserrat";
                        font-size: 18px;
                        transition: .3s;

                        &:disabled {
                            cursor: not-allowed;
                        }
                    }
                }

                @media (max-width: 960px) {
                    .function {
                        flex-direction: column;
                        align-items: center;
                    }
                }
            }

        }

        .right {
            width: 24%;
            padding-bottom: 20px;
            text-align: center;
            margin-bottom: 80px;
            border-radius: 10px;
            margin-top: 5px;
            background-color: #7b68ee;
            box-shadow: 0 3px 15px #0000005e;
            margin: 0;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;

            .wallet {
                margin-top: 20px;
                margin-bottom: 20px;
                border: none;
                border-radius: 30px;
                display: inline-block;
                width: fit-content;
                padding-right: 15px;
                padding-left: 15px;
                height: 50px;
                color: #ffffffaa;
                font-size: 20px;
                font-weight: 900;
                font-family: 'Montserrat';
                top: 6px;
                line-height: 50px;
                background-image: linear-gradient(45deg, #4ce435 0%, #2666d3 100%);
                box-shadow: 0 0 20px #4ce435;
                animation: wallet-animation 3s linear infinite;
                cursor: pointer;
            }

            .info {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .title {
                    font-size: 16px;
                    color: #fff;
                }

                .content {
                    width: 70%;
                    padding: 5px;
                    color: #7b68ee;
                    font-size: 16px;
                    background-color: #ffffffaa;
                    border: 2px solid #3a3e507d;
                    border-radius: 10px;
                }
            }

            .referrer-link {
                margin-top: 20px;
                margin-bottom: 20px;
                border: none;
                border-radius: 30px;
                display: inline-block;
                width: fit-content;
                padding-right: 15px;
                padding-left: 15px;
                height: 50px;
                color: #ffffffaa;
                font-size: 18px;
                font-weight: 900;
                font-family: 'Montserrat';
                top: 6px;
                line-height: 50px;
                background-image: linear-gradient(45deg, rgb(228 53 102) 0%, rgb(235 75 80) 100%);
                box-shadow: 0 0 20px rgb(235 75 80);
                animation: wallet-animation 3s linear infinite;
                cursor: pointer;
            }

        }
        @media (max-width: 960px) {
            .left, .right {
                width: 100%;
            }
        }
    }
    @media (max-width: 960px) {
        .center {
            flex-direction: column;
        }
    }
}

@media (max-width: 960px){
    .transfer{
        // position: fixed;
        left: 0;
        top: 50px;
    }
}