.buy-and-sell {
    width: 100%;
    font-size: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;

    .top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;

        .info-card-1 {
            width: 25%;
            min-height: 70px;
            border-radius: 10px;
            box-shadow: 0 0 20px #ffffff;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            color: #7a4f01;
            background: #fff7cd;

            .title {
                font-size: 14px;
            }

            .content {
                font-size: 16px;
                font-weight: 800;
            }
        }

        .info-card-2 {
            @extend .info-card-1;
            color: #061b64;
            background: #d1e9fc;
        }

        .info-card-3 {
            @extend .info-card-1;
            color: #7a0c2e;
            background: #ffe7d9;
        }

        a {
            @extend .info-card-1;
            background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);

            .title {
                color: white;
                font-size: 18px;
            }

            .content {
                color: #ffffff80;
                font-size: 14px;
                font-weight: 400;
            }
        }

        @media (max-width: 960px) {
            .info-card-1, .info-card-2, .info-card-3, a {
                width: 100%;
            }
        }
    }

    @media (max-width: 960px) {
        .top {
            flex-direction: column;
        }
    }

    .center {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 15px;

        .left {
            width: 76%;

            .list {
                color: #000;
                background-color: #fdffff;
                border-radius: 10px;
                box-shadow: 0 0 20px #ffffff;
                padding: 3px;
                margin-bottom: 15px;
                ;

                .title {
                    border-radius: 5px;
                    padding: 10px;
                }

                table {
                    color: #6e6b7b;
                    font-size: 14px;
                    width: 100%;

                    th {
                        background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);
                        color: #fff;
                        padding: 10px;
                        font-weight: 300;
                    }

                    tbody {
                        tr {
                            td {
                                text-align: center;
                                font-size: 16px;
                                padding: 10px 0;

                                .sell-btn {
                                    height: 25px;
                                    width: 100%;
                                    font-family: 'Montserrat';
                                    color: #fff;
                                    background: #4caf50;
                                    border-radius: 10px;
                                    cursor: pointer;
                                }

                                .selling-btn {
                                    @extend .sell-btn;
                                    background: #aaaaaa;
                                    color: #fff;
                                }
                            }
                        }

                        tr:nth-child(2n) {
                            background-color: #6495ed20;
                        }
                    }

                }

                .content {
                    margin-inline-start: 5%;
                    width: 90%;
                    padding-top: 15px;
                    padding-bottom: 15px;
                    display: flex;
                    gap: 24px;
                    justify-content: flex-start;

                    .sell-card {
                        width: 200px;
                        background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);
                        border-radius: 10px;
                        padding-top: 5px;

                        .days {
                            display: flex;
                            justify-content: space-around;
                            font-size: 14px;
                            color: #f0fff0;
                        }

                        .progress {
                            width: 84%;
                            margin-inline-start: 8%;
                            background-color: #f0fff055;
                            height: 8px;
                            border-radius: 50vmax;
                        }

                        .info-item {
                            font-size: 14px;
                            margin: 5px 0;
                            margin-inline-start: 5%;

                            .info-title {
                                color: #f0fff0;
                            }

                            .info-content {
                                margin-inline-start: 10px;
                                color: #adff2f;
                            }
                        }

                        .buy-item {
                            background: linear-gradient(45deg, #f44576 0%, #fb5b60 100%);
                            box-shadow: 0 3px 4px #00000033, inset 0 0 10px 3px #00000033, 0 3px 20px #e435664d, 0 3px 25px #eb4b507a;
                            border-radius: 0 0 10px 10px;
                            font-size: 14px;
                            font-weight: 600;
                            line-height: 20px;
                            color: #ffffffdd;
                            text-align: center;
                            cursor: pointer;
                        }
                    }
                }

                .warning {
                    padding: 10px;
                    text-align: center;
                    color: #362f2f40;
                }
            }

        }

        .right {
            width: 24%;
            padding-bottom: 20px;
            text-align: center;
            margin-bottom: 80px;
            border-radius: 10px;
            margin-top: 5px;
            background-color: #7b68ee;
            box-shadow: 0 3px 15px #0000005e;
            margin: 0;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;

            .wallet {
                margin-top: 20px;
                margin-bottom: 20px;
                border: none;
                border-radius: 30px;
                display: inline-block;
                width: fit-content;
                padding-right: 15px;
                padding-left: 15px;
                height: 50px;
                color: #ffffffaa;
                font-size: 20px;
                font-weight: 900;
                font-family: 'Montserrat';
                top: 6px;
                line-height: 50px;
                background-image: linear-gradient(45deg, #4ce435 0%, #2666d3 100%);
                box-shadow: 0 0 20px #4ce435;
                animation: wallet-animation 3s linear infinite;
                cursor: pointer;
            }

            .info {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .title {
                    font-size: 16px;
                    color: #fff;
                }

                .content {
                    width: 70%;
                    padding: 5px;
                    color: #7b68ee;
                    font-size: 16px;
                    background-color: #ffffffaa;
                    border: 2px solid #3a3e507d;
                    border-radius: 10px;
                }
            }

            .referrer-link {
                margin-top: 20px;
                margin-bottom: 20px;
                border: none;
                border-radius: 30px;
                display: inline-block;
                width: fit-content;
                padding-right: 15px;
                padding-left: 15px;
                height: 50px;
                color: #ffffffaa;
                font-size: 18px;
                font-weight: 900;
                font-family: 'Montserrat';
                top: 6px;
                line-height: 50px;
                background-image: linear-gradient(45deg, rgb(228 53 102) 0%, rgb(235 75 80) 100%);
                box-shadow: 0 0 20px rgb(235 75 80);
                animation: wallet-animation 3s linear infinite;
                cursor: pointer;
            }

        }

        @media (max-width: 960px) {
            .left, .right {
                width: 100%;
            }
        }
    }

    @media (max-width: 960px) {
        .center {
            flex-direction: column;
        }
    }

    .backdrop {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #00000030;
        display: flex;
        justify-content: center;
        align-items: center;

        .auction-input {
            width: 40%;
            min-width: 300px;
            background: #d1e9fc;
            box-shadow: 0 0 20px #000000aa;
            border-radius: 15px;
            padding: 20px;

            .top {
                width: 100%;
                display: flex;
                justify-content: flex-end;

                img {
                    cursor: pointer;
                }
            }

            .center {
                display: flex;
                flex-direction: column;
                align-items: center;
                font-size: 18px;

                input {
                    background: #fff;
                    border: 3px solid #6b6e7c61;
                    border-radius: 4px;
                    line-height: 40px;
                    width: 250px;
                    padding-left: 10px;
                    box-shadow: 3px 4px 4px #9294a063;
                    font-family: "Montserrat";
                    font-size: 18px;
                    color: #000;
                    font-weight: 500;
                }
            }

            .bottom {
                margin-top: 30px;
                display: flex;
                justify-content: center;

                button {
                    background: #1a8afd;
                    color: #fff;
                    font-family: "Montserrat";
                    width: 90%;
                    line-height: 30px;
                    border-radius: 5px;
                    cursor: pointer;
                }
            }
        }
    }
}

@media (max-width: 960px){
    .buy-and-sell{
        // position: fixed;
        left: 0;
        top: 50px;
    }
}