.staking {
    width: 100%;
    font-size: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;

    .top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 24px;

        .info-card-1 {
            width: 25%;
            min-height: 70px;
            border-radius: 10px;
            box-shadow: 0 0 20px #ffffff;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            color: #7a4f01;
            background: #fff7cd;

            .title {
                font-size: 14px;
            }

            .content {
                font-size: 16px;
                font-weight: 800;
            }
        }

        .info-card-2 {
            @extend .info-card-1;
            color: #061b64;
            background: #d1e9fc;
        }

        .info-card-3 {
            @extend .info-card-1;
            color: #7a0c2e;
            background: #ffe7d9;
        }

        a {
            @extend .info-card-1;
            background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);

            .title {
                color: white;
                font-size: 18px;
            }

            .content {
                color: #ffffff80;
                font-size: 14px;
                font-weight: 400;
            }
        }

        @media (max-width: 960px) {
            .info-card-1, .info-card-2, .info-card-3, a {
                width: 100%;
            }
        }
    }

    @media (max-width: 960px) {
        .top {
            flex-direction: column;
        }
    }

    .center {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 15px;

        .left {
            width: 76%;

            .stake {
                color: #000;
                background-color: #fdffff;
                border-radius: 10px;
                box-shadow: 0 0 20px #ffffff;
                padding-top: 10px;

                .title {
                    margin-left: 10px;
                }

                .intro {
                    margin-top: 10px;
                    margin-left: 10px;
                    font-size: 14px;
                }

                .function {
                    margin-left: 20px;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;

                    input {
                        margin-top: 15px;
                        background: #f3f2f7;
                        border: 3px solid #6b6e7c61;
                        border-radius: 4px;
                        height: 30px;
                        width: 190px;
                        padding-left: 5px;
                        box-shadow: 3px 4px 4px #9294a063;
                        font-family: "Montserrat";
                        color: #abbac6;
                        font-weight: 500;
                    }

                    span {
                        color: red;
                        font-size: 14px;
                    }

                    button {
                        margin: 20px 20px 0;
                        border-radius: 7px;
                        display: inline-block;
                        width: 150px;
                        height: 36px;
                        text-align: center;
                        cursor: pointer;
                        background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);
                        box-shadow: 2px 4px 4px #04297a60;
                        color: #fff;
                        font-family: "Montserrat";
                        font-size: 18px;
                        transition: .3s;

                        &:disabled {
                            cursor: not-allowed;
                        }
                    }
                }

                @media (max-width: 960px) {
                    .function {
                        flex-direction: column;
                        align-items: center;
                    }
                }

                .bottom {
                    margin-top: 20px;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .info {
                        width: 33%;
                        min-height: 70px;
                        border: 1px #ecf1f6 solid;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;

                        .title {
                            color: #8e8e8e;
                            font-size: 14px;
                        }

                        .content {
                            color: black;
                            font-size: 16px;
                        }
                    }

                    @media (max-width: 960px) {
                        .info {
                            width: 100%;
                        }
                    }
                }

                @media (max-width: 960px){
                    .bottom {
                        flex-direction: column;
                    }
                }
            }

            .list {
                margin-top: 10px;
                color: #000;
                background-color: #fdffff;
                border-radius: 10px;
                box-shadow: 0 0 20px #ffffff;
                padding: 3px;

                .title {
                    border-radius: 5px;
                    padding: 10px;
                }

                table {
                    color: #6e6b7b;
                    font-size: 14px;
                    width: 100%;

                    th {
                        background: linear-gradient(45deg, #1a8afd 0%, #309ffe 100%);
                        color: #fff;
                        padding: 10px;
                        font-weight: 300;
                    }

                    tbody {
                        tr {
                            td {
                                text-align: center;
                                font-size: 16px;
                                padding: 10px 0;

                                .sell-btn {
                                    height: 25px;
                                    width: 100%;
                                    background: #4caf50;
                                    font-family: 'Montserrat';
                                    color: #fff;
                                    border-radius: 10px;
                                    cursor: pointer;
                                }

                                .collected-btn, .sold-btn {
                                    @extend .sell-btn;
                                    background: #dc4051;
                                    color: #fff;
                                    cursor: unset;
                                }

                                .collect-btn {
                                    @extend .sell-btn;
                                    background: #6495ed;
                                    color: #fff;
                                }

                                .selling-btn {
                                    @extend .sell-btn;
                                    background: #aaaaaa;
                                    color: #fff;
                                }
                            }
                        }

                        tr:nth-child(2n) {
                            background-color: #6495ed20;
                        }
                    }

                }

                .warning {
                    padding: 10px;
                    text-align: center;
                    color: #362f2f40;
                }
            }

        }

        .right {
            width: 24%;
            padding-bottom: 20px;
            text-align: center;
            margin-bottom: 80px;
            border-radius: 10px;
            margin-top: 5px;
            background-color: #7b68ee;
            box-shadow: 0 3px 15px #0000005e;
            margin: 0;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;

            .wallet {
                margin-top: 20px;
                margin-bottom: 20px;
                border: none;
                border-radius: 30px;
                display: inline-block;
                width: fit-content;
                padding-right: 15px;
                padding-left: 15px;
                height: 50px;
                color: #ffffffaa;
                font-size: 20px;
                font-weight: 900;
                font-family: 'Montserrat';
                top: 6px;
                line-height: 50px;
                background-image: linear-gradient(45deg, #4ce435 0%, #2666d3 100%);
                box-shadow: 0 0 20px #4ce435;
                animation: wallet-animation 3s linear infinite;
                cursor: pointer;
            }

            .info {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .title {
                    font-size: 16px;
                    color: #fff;
                }

                .content {
                    width: 70%;
                    padding: 5px;
                    color: #7b68ee;
                    font-size: 16px;
                    background-color: #ffffffaa;
                    border: 2px solid #3a3e507d;
                    border-radius: 10px;
                }
            }

            .referrer-link {
                margin-top: 20px;
                margin-bottom: 20px;
                border: none;
                border-radius: 30px;
                display: inline-block;
                width: fit-content;
                padding-right: 15px;
                padding-left: 15px;
                height: 50px;
                color: #ffffffaa;
                font-size: 18px;
                font-weight: 900;
                font-family: 'Montserrat';
                top: 6px;
                line-height: 50px;
                background-image: linear-gradient(45deg, rgb(228 53 102) 0%, rgb(235 75 80) 100%);
                box-shadow: 0 0 20px rgb(235 75 80);
                animation: wallet-animation 3s linear infinite;
                cursor: pointer;
            }

        }
        @media (max-width: 960px) {
            .left, .right {
                width: 100%;
            }
        }

    }
    @media (max-width: 960px) {
        .center {
            flex-direction: column;
        }
    }
}

@media (max-width: 960px){
    .staking{
        // position: fixed;
        left: 0;
        top: 50px;
    }
}