$second_color: #7d849e;
.faq {
    padding-top: 100px;
    .title {
        color: white;
        text-align: center;
        font-size: 32px;
        padding-bottom: 30px;
    }

    .faq-item {
        width: 80%;
        margin: auto;
        padding: 15px 0 15px 0;
        border-bottom: 1px solid #3b4360;
        .question {
            color: white;
            display: block;
            width: 100%;
            font-size: 20px;
            line-height: 30px;
            font-weight: 700;
            padding-right: 25px;
            transition: color .4s;
            user-select: none;
            position: relative;
            span {
                position: absolute;
                font-size: 16px;
                right: 7.5px;

                color: #00d1e4;
            }
        }
        .answer {
            padding-top: 10px;
            font-size: 18px;
            line-height: 30px;
            color: $second_color;
            p{
                margin: 0;
                padding: 0;
            }
        }
    }
}