.header {
    // margin: 10px;
    background:#6495ed;
    box-shadow: 0 0 15px #00000050;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;

    .left {
        margin-inline-start: 5%;
        color: white;
        font-size: 25px;

        img {
            height: 50px;
        }
    }

    .center {
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;

        .active {
            color: #6495ed;
            padding: 5px 10px;
            border-radius: 5px;
            background-color: #fff;
        }
    }

    @media (max-width: 960px){
        .center {
            display: none;
        }
    }

    .right {
        margin-inline-end: 5%;
        // background-color: #2c303d;
        line-height: 30px;
        color: #fff;
        border: 1px #b3e1f7 solid;
        padding: 0 10px;
        border-radius: 10px;
    }

    .menu-icon {
        display: none;
    }

    @media (max-width: 960px) {
        .menu-icon {
            display: block;
            width: 16px;
            margin-right: 10px;
        }    
    }
}

.mobile-show {
    display: none;
}

.mobile-menu {
    display: none;
}

.mobile-menu {
    background-color: #6495ed;
    width: 100%;
    padding: 20px 0;
    position: fixed;
    color: white;
    ul{
        list-style-type: none;
        li {
            padding: 5px 0;
            text-align: center;
        }
    }
}

@media (max-width: 960px) {
    .mobile-menu {
        display: block;
    }
    .mobile-show {
        display: block;
    }
}