.event-bar {
    overflow: auto;
    color: white;
    background: #fff;
    box-shadow: 0 0 15px #00000020;
    background: #5484dd;
    width: 230px;
    height: calc(100vh - 50px);
    position: fixed;
    top: 50px;
    left: 0;
    text-align: center;

    .title {
        line-height: 50px;
    }

    .event-list {
        width: 100%;
        height: calc(100vh - 100px);
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        gap: 10px;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 8px;
            border-radius: 50%;
        }

        &::-webkit-scrollbar-thumb {
            background: #edf7ff;
            border-radius: 5px;
          }


        .event-box-1 {
            color: #3b6134;
            background: #cbf1c4;
            width: calc(90% - 10px);
            padding: 5px;
            font-size: 16px;
            border-radius: 10px;

            .address {
                background: #00000020;
                border-radius: 10px;
            }

            .time {
                color: #00000066;
                text-align: right;
            }
        }

        .event-box-2 {
            @extend .event-box-1;
            color: #061b64;
            background: #d1e9fc;
        }

        .event-box-3 {
            @extend .event-box-1;
            color: #7a0c2e;
            background: #ffe7d9;
        }

        .event-box-4 {
            @extend .event-box-1;
            color: #064f61;
            background: #ffe7d9;
        }

        .event-box-5 {
            @extend .event-box-1;
            color: #3b3805;
            background: #ffe7d9;
        }
    }
}

.close-icon {
    display: none;
}

@media (max-width: 960px){
    .event-bar {
        display: none;
    }
    .mobile-show {
        .event-bar {
            display: block;
            .close-icon {
                display: block;
                margin-top: 10px;
                img {
                    width: 20px;
                }
            }
        }
    }
}

